.board {
    background-color: burlywood;
    border: 0.2em solid red;
    height: 90%;
}

.row {
    display: flex;
    border: 0.2em solid orange;
    height: 90%;
}

.column {
    flex: 50%;
    border: 0.2em solid lightblue;
    height: 90%;
}

.block {
    border: 0.2em solid blue;
    height: 32vh;
}

.block button {
    font-size: 8em;
    position: relative;
    padding: 0.3em;
    top: 20%;
}

.blockstatus button {
    font-size: 8em;
    position: relative;
    padding: 0.15em;
    top: 3%;
}